import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition } from '../../../../../../../Bamba.Shared/src/models/field-definition';
import { MetaDataType } from '../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { Style } from '../models/style';

@Component({
  selector: 'app-styles',
  templateUrl: './styles.component.html',
  styleUrls: ['./styles.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StylesComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];
  fieldName: FieldDefinition;

  style: Style;
  routeDataType: string;

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private hydraApi: HydraApiService,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit() {
    this.createButtons();
    this.createFields();
    this.getStyleData();
  }

  getStyleData() {
    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeDataType = data.type;

        if (['properties', 'duplicate'].includes(this.routeDataType)) {
          this.spinner.show(this.translate.instant('Common.Loading'), document.documentElement);
          this.hydraApi.getStyle(params.styleId, this.styleLoaded, this.styleLoadFailed);
        }

        if (this.routeDataType === 'create') {
          this.styleLoaded(new Style());
        }
      })
    );
  }

  private styleLoaded = (s: Style) => {
    if (this.routeDataType === 'duplicate') {
      s.name = `${this.translate.instant('Admin.CopyOf')} ${s.name}`;
      s.id = '';
    }

    this.style = s;
    this.spinner.hide();
  };

  private styleLoadFailed = () => {
    this.toastr.error(this.translate.instant('admin-olc-styles.style-load-failed'));
    this.spinner.hide();
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-olc-styles.name'),
      placeholder: this.translate.instant('admin-olc-styles.name-placeholder'),
      required: true,
    });
  };

  private createButtons() {
    const saveButton = new Control(
      this.onSave,
      this.translate.instant('admin-olc-styles.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    const cancelButton = new Control(
      this.onCancel,
      this.translate.instant('admin-olc-styles.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.rightControls.push(saveButton);
    this.rightControls.push(cancelButton);
  }

  onSave = () => {
    this.spinner.showSavingSpinner();

    this.style.inlineStyles = this.style.inlineStyles.filter(
      (inlineStyle) => inlineStyle.property || inlineStyle.value
    );

    if (this.style.id) {
      this.hydraApi.updateStyle(this.style, this.styleSaved, this.styleSaveFailed);
    } else {
      this.hydraApi.createStyle(this.style, this.styleSaved, this.styleSaveFailed);
    }
  };

  onCancel = () => {
    this.returnToGrid();
  };

  styleSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-olc-styles.style-saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-olc-styles.validation-errors');
    }

    this.spinner.hide();
  };

  styleSaveFailed = (err: any) => {
    this.toastr.error(this.translate.instant('admin-olc-styles.style-save-failed'));
    this.spinner.hide();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'product/settings/BambaOLCTEDApplication/BambaOLCTEDApplication-Upland.Bamba.OLCTed.Styles.StylesFolder'
    );
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
