import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, IFieldOptions } from '../../../../../../../../Bamba.Shared/src/models/field-definition';
import { MetaDataType } from '../../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { Style } from '../../models/style';
import { InlineStyle, StyleType } from '../../models/template';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { DelayService } from '../../../../../../../../Bamba.Shared/src/utils/delay.service';
import { ErrorsService } from './../../../../blocks/services/errors.service';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { FixedDataSource } from '../../../../../../../../Bamba.Shared/src/models/fixed-datasource';

@Component({
  selector: 'app-properties-style',
  templateUrl: './properties-style.component.html',
  styleUrls: ['./properties-style.component.scss'],
})
export class PropertiesStyleComponent implements OnInit {
  @Input() style: Style;
  @ViewChild(PerfectScrollbarDirective) directiveRef?: PerfectScrollbarDirective;

  elementField: FieldDefinition;
  propertyField: FieldDefinition;
  valueField: FieldDefinition;

  propertyFieldFocused = true;
  newInlineStyle = new InlineStyle({ property: '', value: '' });
  inlineElements = ['span', 'b', 'em', 'i', 'strong'];
  inlineOptions: IFieldOptions[];
  blockElements = ['p', 'div', 'h1', 'h2', 'h3', 'h4', 'pre'];
  blockOptions: IFieldOptions[];
  elementDataSource: FixedDataSource;

  constructor(
    private translate: TranslateService,
    private errorsService: ErrorsService,
    private delay: DelayService,
    public ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit() {
    this.inlineOptions = this.inlineElements.map((element) => ({ name: element, value: element }));
    this.blockOptions = this.blockElements.map((element) => ({ name: element, value: element }));
    this.elementDataSource = new FixedDataSource(this.inlineOptions);

    this.createFields();

    if (this.style.elementType == StyleType.None) this.style.elementType = StyleType.Inline;
    this.elementTypeSelected(this.style.elementType);
  }

  createFields = () => {
    this.elementField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-olc-styles.element-label'),
      datasource: this.elementDataSource,
      required: true,
    });

    this.propertyField = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-olc-styles.property-label'),
      placeholder: this.translate.instant('admin-olc-styles.property-placeholder'),
      required: true,
    });

    this.valueField = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-olc-styles.value-label'),
      placeholder: this.translate.instant('admin-olc-styles.value-placeholder'),
      required: true,
    });
  };

  createTextField = (placeholder: string) => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      placeholder: this.translate.instant(placeholder),
      required: true,
    });
  };

  onValueReturn() {
    this.onAddInlineStyle();
  }

  onAddInlineStyle = () => {
    if (!this.validateNewInlineStyle()) return;

    this.style.inlineStyles.push(new InlineStyle({ ...this.newInlineStyle }));
    this.newInlineStyle.property = '';
    this.newInlineStyle.value = '';
    this.propertyFieldFocused = false;

    this.delay.execute(() => {
      this.directiveRef.scrollToBottom(0, 300);
      this.propertyFieldFocused = true;
    }, 100);
  };

  validateNewInlineStyle(): boolean {
    let validationErrors: string[] = [];

    const propertyErrors = this.validateProperty();
    validationErrors = validationErrors.concat(propertyErrors);

    const valueErrors = this.validateValue();
    validationErrors = validationErrors.concat(valueErrors);

    if (!validationErrors.length) return true;

    this.errorsService.showErrorsModal(validationErrors, 'admin-olc-styles.style-validation-errors');
    return false;
  }

  validateProperty(): string[] {
    const errors = [];

    if (!this.newInlineStyle.property.trim().length) {
      errors.push('admin-olc-styles.property-required');
    }

    return errors;
  }

  validateValue(): string[] {
    const errors = [];

    if (!this.newInlineStyle.value.trim().length) {
      errors.push('admin-olc-styles.value-required');
    }

    return errors;
  }

  deleteAllInlineStyles = (): void => {
    this.style.inlineStyles = [];
  };

  deleteInlineStyle = (style: InlineStyle) => {
    const rowIndexToDelete = this.style.inlineStyles.indexOf(style);

    if (rowIndexToDelete > -1) {
      this.style.inlineStyles.splice(rowIndexToDelete, 1);
    }
  };

  elementTypeSelected(type: StyleType) {
    if (type === StyleType.Inline) {
      this.elementDataSource.updateOptions(this.inlineOptions);
      if (this.style.elementType != type) this.style.element = this.inlineElements[0];
    } else {
      this.elementDataSource.updateOptions(this.blockOptions);
      if (this.style.elementType != type) this.style.element = this.blockElements[0];
    }

    this.style.elementType = type;
  }
}
