import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from 'shared-code-lib';
import { Style } from '../../models/style';

@Component({
  selector: 'app-properties-form',
  templateUrl: './properties-form.component.html',
  styleUrls: ['./properties-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PropertiesFormComponent implements OnInit {
  @Input() style: Style;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.tabs.push(new Tab('general', this.translateService.instant('admin-olc-templates.general')));
  }

  activateTab = (tab) => {
    this.activeTab = tab;
  };
}
